html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

p {
  margin: 0;
}

h1, h2 {
  margin: 0;
}

.gradient {
  background: linear-gradient(to right, #4A16CF 0%, #0F6CCF 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: tomato;
}

/** Media Queries **/
.header {
  background-image: url(../../src/img/header-bg.svg);
  background-repeat: no-repeat;
  background-position: right top;
  background-size: 100rem;
  padding: 5rem 0;
  height: 30rem;
  background-color: rgb(237.75, 237.75, 237.75);
}

@media (min-width: 768px) {
  .header {
    background-image: url(../../src/img/header-bg.svg);
    background-size: 100rem;
    height: auto;
  }
}
.header__logo h1 {
  color: #fff;
}

@media (min-width: 768px) {
  .header__logo h1 {
    color: #8c4fff;
  }
}
.header__barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
    max-width: 90%;
    margin: 0 auto;
  }
}
.navegacion {
  gap: 30rem;
}

@media (min-width: 768px) {
  .navegacion {
    display: flex;
    flex-direction: row;
  }
}
.navegacion__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  font-family: "IBM Plex Sans", sans-serif;
}

@media (min-width: 768px) {
  .navegacion__contenedor {
    display: flex;
    flex-direction: row;
    gap: 3rem;
  }
}
.navegacion__link:last-of-type {
  margin-bottom: 0;
}

.navegacion__link:hover {
  color: #e1e1e1;
}

.header__contenedor {
  margin-bottom: 5rem;
}

@media (min-width: 768px) {
  .navegacion__login {
    display: flex;
    gap: 5rem;
  }
}
.header__grid {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media (min-width: 768px) {
  .header__grid {
    max-width: 90%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.header__text {
  text-align: center;
  font-weight: 300;
}
.header__text span {
  font-weight: 700;
}

.header__texto {
  margin-top: 5rem;
}

.header__heading {
  font-family: "IBM Plex Sans", sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  font-size: 1.5rem;
  color: rgb(148.5, 148.5, 148.5);
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  .header__heading,
  .header__text {
    text-align: left;
    line-height: 1.5;
  }
  .header__text {
    font-size: 3.5rem;
  }
}
@media (min-width: 768px) {
  .header__imagen {
    max-width: 70rem;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .searchBar {
    width: 100%;
    max-width: 70rem;
    background-color: #fff;
    display: flex;
    align-items: center;
    border-radius: 6rem;
    padding: 1rem 2rem;
    box-shadow: 0px 0px 2.6rem -0.8rem #7b7b7b;
  }
  .searchBar input {
    background: transparent;
    flex: 1;
    border: none;
    outline: none;
    padding: 1rem 2rem;
    font-size: 2rem;
  }
  .searchBar button {
    border: none;
    background-color: transparent;
  }
  .searchBar button img {
    width: 5rem;
    border-style: none;
  }
  .searchBar button img:hover {
    cursor: pointer;
  }
  .header__searchBar {
    margin-top: 3rem;
  }
}
.main {
  background: linear-gradient(to bottom, rgb(237.75, 237.75, 237.75) 60%, #fff 60% 100%);
}
.main__txt {
  font-size: 1.5rem;
  font-family: "IBM Plex Sans", sans-serif;
  text-transform: uppercase;
  color: rgb(148.5, 148.5, 148.5);
  margin-bottom: 4rem;
}
.main__container {
  max-width: 90%;
  margin: 0 auto;
  margin-bottom: 5rem;
}
.main__grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 0.1rem;
  justify-items: center;
}

.card {
  border-radius: 10px;
  background: rgb(250.5, 250.5, 250.5);
  padding: 2rem 3rem;
  width: 35rem;
  text-align: center;
  box-shadow: 0px 0px 2.6rem -0.8rem #aeaeae;
}
.card__image {
  padding: 5rem;
  margin-bottom: 4rem;
}
.card__image img {
  width: 20rem;
  margin: 0 auto;
}
.card__location {
  display: flex;
  flex-direction: row;
}
.card__description .card__location img {
  width: 1.5rem;
}
.card__description p {
  align-items: center;
  color: rgb(97.5, 97.5, 97.5);
}
.card__description .card__bussiness {
  font-weight: 600;
}
.card__paymnent {
  margin-top: 4rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  text-align: start;
}
.card__paymnent p {
  margin-bottom: 1rem;
}
.card__paymnent p:last-of-type {
  margin-bottom: 0;
}
.card__text {
  text-align: start;
}
.card__text p {
  margin-bottom: 1rem;
}
.card__text .card__job {
  font-weight: 500;
}
.card__text p img {
  width: 2rem;
}
.card__button {
  display: flex;
  justify-content: center;
}
.card__button button {
  border: 1px solid #e1e1e1;
  border-radius: 50%;
  background-color: #fff;
  width: 6rem;
  color: #4A16CF;
  transition: background-color 0.3s ease;
}
.card__button button:hover {
  background-color: rgba(74, 22, 207, 0.9254901961);
  color: #fff;
  cursor: pointer;
}

.gradient {
  font-size: 2.5rem;
  font-weight: 500;
}