@use 'variables.scss' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body{
    font-family: v.$fuentePrincipal;
    font-size: 1.6rem;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
img {
    max-width: 100%;
    display: block;
}
ul {
    padding:0;
    list-style: none;
    margin: 0;
}

a {
    text-decoration: none;
}

img {
    max-width: 100%;
    display: block;
}
p{
    margin: 0;
}

h1,h2{
    margin: 0;
}
